import uniRequest from 'uni-request'
export default {
	data() {
		return {
			areaList:{
				province_list:[],
				city_list:[],
				county_list:[]
			},
			addressData: {
				name: '',
				mobile: '',
				addressName: '在地图选择',
				address: '',
				area: '',
				default: false
			},
			manageType: {},
			form_address:{},
			default_address_id:false
		}
	},
	onLoad(option) {
		console.log(option)
		let title = '新增收货地址';
		this.addressData.default = false
		if (option.type === 'edit') {
			title = '编辑收货地址'
			this.addressData = JSON.parse(option.data)
			this.default_address_id = true
			if(this.addressData.default_address_id === true){
				this.addressData.default = true
			}else{
				this.addressData.default = false
			}
		}
		this.manageType = option.type;
		uni.setNavigationBarTitle({
			title
		})
		
		this.getAreaList()
		
	},
	methods: {
		async switchChange(e) {
			console.log(e)
			if(e.detail.value === true){
				const response = await uniRequest({
					url: '/addresses/' + this.addressData.id + '/status/',
					method: 'put',
					headers: {
						Authorization: 'JWT ' + uni.getStorageSync('userInfo').token
					},
				}).then(response => {
					console.log(response)
					/* this.$api.prePage().refreshList(data); */
					if(response.status === 200){
					  this.$api.msg('默认地址设置成功')
					  setTimeout((e) => {
					  	uni.navigateBack()
					  }, 800)
					}else{
						this.$api.msg('error')
					}
				}).catch(error => {
					console.log(error.response);
					if(error.response === undefined){
						this.$api.msg('服务器错误');
					}
				})
			}else{
				this.$api.msg('请至少有一个地址为默认');
				setTimeout(() => {
					uni.navigateBack()
				}, 800)
				
			}
			this.addressData.default = e.detail;
		},
		async getAreaList(){
			const response = await uniRequest({
				url: '/mobile/get/carts/name/all/',
				method: 'get',
				headers: {
					Authorization: 'JWT ' + uni.getStorageSync('userInfo').token
				},
			}).then(response => {
				console.log(response)
				var _that = this
				response.data.map(function(e) {
					_that.areaList.province_list[e.id] = e.name;
					e.subs.map(function(o) {
						_that.areaList.city_list[o.id] = o.name;
						o.subs.map(function(a) {
							_that.areaList.county_list[a.id] = a.name;
						})
					})
				})
				console.log(_that.areaList)
			}).catch(error => {
				console.log(error)
			})
		},
		//地图选择地址
		chooseLocation() {
			uni.chooseLocation({
				success: (data) => {
					console.log(data)
					console.log(this.getArea(data.address))
					this.addressData.addressName = data.address + ' ' + data.name;
					this.addressData.address = data.address;
				}
			})
		},

		async setAddress(content) {
			console.log(content)
			this.getProvinceId(content.province)
			this.getCityId(content.city)
			this.getDistrictId(content.district)
			
			const response = await uniRequest({
				url: '/addresses/' + this.addressData.id + '/',
				data: content,
				method: 'put',
				headers: {
					Authorization: 'JWT ' + uni.getStorageSync('userInfo').token
				},
			}).then(response => {
				console.log(response)
			}).catch(error => {
				console.log(error)
			})
		},
		async getAddress(content){
			console.log(content)
			console.log(this.addressData)
			this.form_address = {
			  title:this.addressData.receiver,
			  receiver: this.addressData.receiver,
			  province_id: this.addressData.province_id,
			  city_id: this.addressData.city_id,
			  district_id: this.addressData.district_id,
			  place: this.addressData.place,
			  mobile: this.addressData.mobile,
			  addresses:content.addresses,
			  addressName:content.addressName,
			}
			const response = await uniRequest({
				url: '/addresses/',
				method: 'post',
				data:this.form_address,
				headers: {
					Authorization: 'JWT ' + uni.getStorageSync('userInfo').token
				},
			}).then(response => {
				console.log(response)
			}).catch(error => {
				console.log(error)
			})
		},

		getProvinceId(province) {
			console.log(province)
			var obj = this.areaList.province_list;
			for (var key in obj) {
				if (obj[key] === province) {
					this.addressData.province_id = parseInt(key)
					console.log(this.addressData.province_id)
				}
			}
		},
		getCityId(city) {
			console.log(city)
			var obj = this.areaList.city_list;
			for (var key in obj) {
				if (obj[key] === city) {
					this.addressData.city_id = parseInt(key)
					console.log(this.addressData.city_id)
				}
			}
		},
		getDistrictId(district) {
			console.log(district)
			var obj = this.areaList.county_list;
			for (var key in obj) {
				if (obj[key] === district) {
					this.addressData.district_id = parseInt(key)
					console.log(this.addressData.district_id)
				}
			}
		},

		//省市区截取
		getArea(str) {
			let area = {}
			let index11 = 0
			let index1 = str.indexOf("省")
			if (index1 == -1) {
				index11 = str.indexOf("自治区")
				if (index11 != -1) {
					area.Province = str.substring(0, index11 + 3)
				} else {
					area.Province = str.substring(0, 0)
				}
			} else {
				area.Province = str.substring(0, index1 + 1)
			}

			let index2 = str.indexOf("市")
			if (index11 == -1) {
				area.City = str.substring(index11 + 1, index2 + 1)
			} else {
				if (index11 == 0) {
					area.City = str.substring(index1 + 1, index2 + 1)
				} else {
					area.City = str.substring(index11 + 3, index2 + 1)
				}
			}

			let index3 = str.lastIndexOf("区")
			if (index3 == -1) {
				index3 = str.indexOf("县")
				area.Country = str.substring(index2 + 1, index3 + 1)
			} else {
				area.Country = str.substring(index2 + 1, index3 + 1)
			}
			return area;
		},

		//提交
		async confirm(data) {
			console.log(data)
			
			if (!data.receiver) {
				this.$api.msg('请填写收货人姓名');
				return;
			}
			if (!/(^1[3|4|5|7|8][0-9]{9}$)/.test(data.mobile)) {
				this.$api.msg('请输入正确的手机号码');
				return;
			}
			if (!data.addressName) {
				this.$api.msg('请在地图选择所在位置');
				return;
			}
			if (!data.place) {
				this.$api.msg('请填写详细地址信息');
				return;
			}
			this.addressData.province = this.getArea(data.addressName).Province
			this.addressData.city = this.getArea(data.addressName).City
			this.addressData.district = this.getArea(data.addressName).Country
			this.addressData.address = data.address
			this.addressData.addressName = data.addressName
			this.addressData.receiver = data.receiver
			this.addressData.title = data.title
			console.log(this.manageType)
			if(this.manageType === 'edit'){
				this.addressData.address = data.address
				this.setAddress(data)
			}else if(this.manageType === 'add'){
				console.log(this.getArea(data.address).province)
				if(this.addressData.province === ''){
					this.addressData.province = this.addressData.city
				}
				this.getProvinceId(this.addressData.province)
				this.getCityId(this.addressData.city)
				this.getDistrictId(this.addressData.district)
				this.getAddress(data)
			}
			
			//this.$api.prePage()获取上一页实例，可直接调用上页所有数据和方法，在App.vue定义
			this.$api.prePage().refreshList(data, this.manageType);
			this.$api.msg(`地址${this.manageType=='edit' ? '修改': '添加'}成功`);
			setTimeout(() => {
				uni.navigateBack()
			}, 800)
		},
		
		// 删除
		async condelete(data){
			const response = await uniRequest({
				url: '/addresses/' + this.addressData.id + '/',
				method: 'delete',
				headers: {
					Authorization: 'JWT ' + uni.getStorageSync('userInfo').token
				},
			}).then(response => {
				console.log(response)
				this.$api.prePage().refreshList(data);
				this.$api.msg('删除成功')
				setTimeout(() => {
					uni.navigateBack()
				}, 800)
				this.addresses.splice(index, 1);
			}).catch(error => {
				console.log(error)
			})
		}
	}
}
